<template>
    <div class="container back-one">
        <div class="transfer-page payment-page" v-if="skInfoList">
            <div class="di-money">最低限额：<i class="red">{{skInfoList[select]['minMoney']}}</i>元 , 最高限额: <i class="red">{{skInfoList[select]['maxMoney']}}</i>元</div>
            <ul>
                <li @click="show=true">
                    <span>收款信息</span>
                    <div class="txt"></div>
                    <div class="fr">
                        <i class="ico-i1"></i>
                    </div>
                </li>
                <li>
                    <span>收款方:</span>
                    <div class="txt c1">{{skInfoList[select]['account']}}</div>
                    <div class="fr">
                        <button
                            v-clipboard:copy="skInfoList[select]['account']"
                            v-clipboard:success="onCopy"
                            >复制</button>
                    </div>
                </li>
                <li>
                    <span>收款银行：</span>
                    <div class="txt c1">{{skInfoList[select]['bankCardName']}}</div>
                    <div class="fr">
                        <button
                            v-clipboard:copy="skInfoList[select]['bankCardName']"
                            v-clipboard:success="onCopy"
                            >复制</button>
                    </div>
                </li>
                <li>
                    <span>收款人：</span>
                    <div class="txt c1">{{skInfoList[select]['username']}}</div>
                    <div class="fr">
                        <button
                            v-clipboard:copy="skInfoList[select]['username']"
                            v-clipboard:success="onCopy"
                            >复制</button>
                    </div>
                </li>
                <li>
                    <span>本次额度：</span>
                    <div class="txt c1">{{money}}</div>
                    <div class="fr">
                        
                    </div>
                </li>
            </ul>
            <div class="submit">
                <button @click="submitNext">转账完成，立即入款</button>
                <p>
                    <router-link to="/bankTransfer" replace>大额充值</router-link>
                    <a href="#">联系客服</a>
                </p>
            </div>
        </div>
        <loadding v-else></loadding>

        <van-popup
            v-model="show"
            position="bottom"
            :style="{ height: '40%' }"
        >
        <van-picker
            title="收款信息"
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="show=false"
        />
        </van-popup>

        <!-- 教程弹窗 -->
        <div class="alert-course" v-if="show2">
            <div class="layout">
                <h2>付款教程<var @click="show2=false">关闭</var></h2>
                <div class="box">
                    <div class="scroll">
                        <p><i>1、</i>打开微信，右上角+，点击扫一扫二维码进行转账</p>
                        <img src="../../static/img/wx/wxyhk_1.png" alt="">
                        <p><i>2、</i>添加收款人微信号（如：田伟伟），进行微信转账</p>
                        <img src="../../static/img/wx/wxyhk_2.png" alt="">
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                show: true,
                show2: false,

                select: 0,
                skInfoList: false,

                url:'',
            }
        },
        created(){
            this.getPayOnline3()
        },
        computed:{
            ...mapState('Recharge',{
                user: state => state.user,
                money: state => state.money
            }),
            columns(){
                var arr = []
                if(this.skInfoList){
                    this.skInfoList.forEach(item=>{
                        arr.push(`${item.account}-${item.username}`)
                    })
                }
                return arr
            }
        },
        methods: {
            returnBack(){
                window.webkit.messageHandlers.back.postMessage("1");
            },
            submitNext(){
                this.$store.commit('Recharge/SET_SELECT', this.skInfoList[this.select])
                // 跳转
                this.$router.replace('/paymentConfirm')
            },
            onConfirm(value, index){
                this.select = index
                this.show = false
            },

            openWx(){
                // window.open('weixin://')
                var json = 'wx'
                var env = this.$assist.getUserAgent()
                if (env.isAndroid) {
                    window.android.openChat(json);
                }
                if (env.isIOS) {
                    window.webkit.messageHandlers.openChat.postMessage(json);
                }
            },
            onCopy(){
                this.$toast('复制成功')
            },

            getPayOnline3(){
                var data = {
                    ...this.user,
                    type: 10,
                }
                this.$api.recharge.getPayOnline3(data)
                .then(res=>{
                    console.log(res.skInfoList);
                    this.skInfoList = res.skInfoList
                })
            }
        },
        
    }
</script>

<style>

</style>